.container{
    width: 100%;
    height: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.image-div{
    min-height: 200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.image-css{
    height: 200px;
    width: 300px;
    margin: 10px;
}

.loading-div{
    height: 100px;
    margin: 20px;
    text-align: center;
    border: 1px solid;
}

.post{
    background-color: #eee;
    margin: 2px;
    padding: 1px;
}
.pagination{
    margin: 2px auto;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    width: 50%;
}
:local(.active){
    border: 1px solid black;
    border-radius: 100%;
    padding: 0 3px;
    outline: none;
}

.scroll{
    background: lightcyan;
    width: 90%;
    height: 100%;
}