.accordion{
    width: 60%;
    background: lightcoral;
    margin: 10px auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

    .accordionItem{
        width: 100%;
        padding: 3px;

        .accordionHeader{
            height: 50px;
            width: 100%;
            background: blueviolet;
            color: white;
            border-radius: 3px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 10px;
            cursor: pointer;
            border: none;

            &.headerActive{
                background: darkblue;
            }
            .icon{
                transition: transform 0.3s ease;
                path {
                    stroke: #fff;
                }

                &.iconActive{
                    transform: rotate(180deg);
                }
            }
        }

        .accordionBody{
            padding: 0 3px;
            background: #f3f3f3;
            max-height: 0;
            overflow: hidden;
            transition: all 1s ease;

            &.activeBody{
                max-height: fit-content;
            }
        }
    }
}