.container{
    background: rgb(240, 230, 230);
    color: #323232;
    font-weight: 300;
    height: 92vh;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: Helvetica neue, roboto;

    .progressDiv {
        height: 8px;
        width: 100%;
        background: #eee;
        border: 1px solid yellow;
        padding: 2px 1px;

        .progressBar {
            height: 100%;
            background-image: linear-gradient(to right, blue, lightblue);

            &.IDLE {
                width: 0%;
            }
            &.LOADING {
                width: 95%;
                transition: width 1s ease-in-out;
            }
            &.COMPLETED {
                width: 100%;
                transition: width 100ms ease-in;
            }
        }
    }
}